<template>
  <div class="base-pie-charts" ref="basePieCharts"></div>
</template>

<script>
export default {
  name: "base-pie-charts",
  components: {},
  props: {
    legendData: {
      type: Array,
      default: () => {
        [];
      }
    },
    chartsData: {
      type: Array,
      default: () => {
        [];
      }
    }
  },
  data() {
    return {};
  },
  mounted() {
    this.init();
  },
  methods: {
    init() {
      let option = {
        title: {
          show: false,
          text: "",
          subtext: "",
          left: "center"
        },
        tooltip: {
          trigger: "item",
          formatter: "{b} : {c} ({d}%)"
        },
        legend: {
          orient: "vertical",
          bottom: "bottom",
          // data: ['直接访问', '邮件营销', '联盟广告', '视频广告', '搜索引擎']
          data: this.legendData
        },
        series: [
          {
            name: "",
            type: "pie",
            radius: "60%",
            center: ["50%", "50%"],
            color: [
              "#00d7b7",
              "#00b4e2",
              "#2a9a97",
              "#3b7cfc",
              "#f69e30",
              "#b1f834",
              "#ff8000"
            ],
            data: this.chartsData,
            label: {
              normal: {
                formatter: "{d}%"
              }
            },
            // [
            //   { value: 335, name: '直接访问' },
            //   { value: 310, name: '邮件营销' },
            //   { value: 234, name: '联盟广告' },
            //   { value: 135, name: '视频广告' },
            //   { value: 1548, name: '搜索引擎' }
            // ],
            emphasis: {
              itemStyle: {
                shadowBlur: 10,
                shadowOffsetX: 0,
                shadowColor: "rgba(0, 0, 0, 0.5)"
              }
            }
          }
        ]
      };
      let echarts = require("echarts");
      this.mychart = echarts.init(this.$refs.basePieCharts);
      this.mychart.setOption(option);
      let mychart = echarts.init(this.$refs.basePieCharts, true); //监听窗口变化重新加载
      window.addEventListener("resize", mychart.resize); //监听窗口变化重新加载

      //点击事件
      let _this = this;
      this.mychart.off("click");
      this.mychart.on("click", function(params) {
        window.console.log(params);
      });
    }
  }
};
</script>

<style scoped lang="scss">
.base-pie-charts {
  height: 100%;
  width: 100%;
}
</style>