<template>
  <div class="base-bar-charts">
    <div ref="baseBarCharts" class="baseBarCharts"></div>
  </div>
</template>

<script>
export default {
  name: "base-bar-charts",
  components: {},
  props: {
    chartsData: {
      type: Array,
      default: () => {
        [];
      }
    },
    xNameData: {
      type: Array,
      default: () => {
        [];
      }
    },
    showClick: {
      type: Boolean,
      default: () => {
        false;
      }
    },
    legendData: {
      type: Array,
      default: () => {
        [];
      }
    }
  },
  data() {
    return {};
  },
  mounted() {
    this.init();
  },
  methods: {
    init() {
      let option = {
        color: ["#3398DB"],
        tooltip: {
          trigger: "axis",
          axisPointer: {
            // 坐标轴指示器，坐标轴触发有效
            type: "shadow" // 默认为直线，可选为：'line' | 'shadow'
          }
        },
        grid: {
          left: "3%",
          right: "4%",
          bottom: "3%",
          containLabel: true
        },
        legend: {
          show: Boolean(this.legendData.length),
          data: this.legendData
        },
        xAxis: [
          {
            type: "category",
            name: "",
            data: this.xNameData,
            axisTick: {
              show: true //坐标轴刻度线
            },
            axisLine: {
              //轴线
              show: false
            },
            splitLine: {
              //网格线
              show: true
            },
            axisLabel: {
              //坐标轴样式
              // interval: 0,
              rotate: 18, //倾斜度 -90 至 90 默认为0
              // margin:2,
              textStyle: {
                color: "#636363",
                fontWeight: "bolder",
                fontSize: 11
              }
              // formatter: function (value) {
              //   //x轴的文字改为竖版显示
              //   var str = value.split("");
              //   return str.join("\n");
              // }
            }
          }
        ],
        yAxis: [
          {
            type: "value",
            name: "",
            axisLine: {
              show: false
            },
            axisTick: {
              show: false
            },
            axisLabel: {
              textStyle: {
                color: "#636363"
              }
            }
          }
        ],
        series: [
          {
            name:
              this.legendData && this.legendData.length
                ? this.legendData[0]
                : "",
            type: "bar",
            barWidth: "60%", // 柱图宽度
            barMaxWidth: 50, // 最大宽度
            showBackground: true,
            backgroundStyle: {
              color: "rgba(199,231,255,0.6)"
            },
            data: this.chartsData
          }
        ]
      };
      let echarts = require("echarts");
      this.mychart = echarts.init(this.$refs.baseBarCharts);
      this.mychart.setOption(option);
      let mychart = echarts.init(this.$refs.baseBarCharts, true); //监听窗口变化重新加载
      window.addEventListener("resize", mychart.resize); //监听窗口变化重新加载

      //点击事件
      let _this = this;
      this.mychart.off("click");
      this.mychart.on("click", function(params) {
        window.console.log(params);
        if (this.showClick) {
          _this.$emit("changeBarDetail", params.name);
        }
      });
      this.mychart.getZr().on("click", function(params) {
        var pointInPixel = [params.offsetX, params.offsetY];
        if (
          _this.showClick &&
          _this.mychart.containPixel("grid", pointInPixel)
        ) {
          let xIndex = _this.mychart.convertFromPixel({ seriesIndex: 0 }, [
            params.offsetX,
            params.offsetY
          ])[0];
          _this.$emit("changeBarDetail", _this.xNameData[xIndex]);
        }
      });
    }
  }
};
</script>

<style scoped lang="scss">
.base-bar-charts {
  height: 100%;
  width: 100%;
  .baseBarCharts {
    height: 100%;
  }
}
</style>