<template>
  <div class="distributionCharts">
    <div class="chartsCon33">
      <div class="charts-title">发生过重复通知抱怨分布</div>
      <div class="chartsItem">
        <base-pie-charts
          v-if="cftzVisible"
          :chartsData="cftzChartsData"
          :lengendData="cftzLengendData"
        ></base-pie-charts>
      </div>
    </div>
    <div class="chartsCon33">
      <div class="charts-title">发生过无通知抱怨分布</div>
      <div class="chartsItem">
        <base-pie-charts
          v-if="wtzVisible"
          :chartsData="wtzChartsData"
          :lengendData="wtzLengendData"
        ></base-pie-charts>
      </div>
    </div>
    <div class="chartsCon33">
      <div class="charts-title">计划停电前3天来电咨询停电相对比分布</div>
      <div class="chartsItem">
        <base-bar-charts v-if="jhtdVisible" :chartsData="jhtdChartsData" :xNameData="jhtdXNameData"></base-bar-charts>
      </div>
    </div>
    <div class="chartsCon50">
      <div class="charts-title">联系号码类型分布</div>
      <div class="chartsItem">
        <base-pie-charts
          v-if="lxhmVisible"
          :chartsData="lxhmChartsData"
          :lengendData="lxhmLengendData"
        ></base-pie-charts>
      </div>
    </div>
    <div class="chartsCon50">
      <div class="charts-title">当前停电第一次短信发送失败分布</div>
      <div class="chartsItem">
        <base-pie-charts
          v-if="dqtdVisible"
          :chartsData="dqtdChartsData"
          :lengendData="dqtdLengendData"
        ></base-pie-charts>
      </div>
    </div>
  </div>
</template>

<script>
import baseBarCharts from '@/components/charts/base-bar-charts'
// import baseLineCharts from '@/components/charts/base-line-charts'
import basePieCharts from '@/components/charts/base-pie-charts'
import Interface from '@/base/mixin/power-sensitive-users';

export default {
  name: "distributionCharts",
  components: {
    baseBarCharts,
    // baseLineCharts,
    basePieCharts,
  },
  props: {},
  mixins: [Interface],

  data() {
    return {
      //发生过重复通知抱怨分布
      cftzChartsData: [],
      cftzLengendData: [],
      cftzVisible: false,
      //发生过无通知抱怨分布
      wtzChartsData: [],
      wtzLengendData: [],
      wtzVisible: false,
      // 计划停电前3天来电咨询停电相对比分布
      jhtdChartsData: [],
      jhtdXNameData: [],
      jhtdVisible: false,
      // 联系号码类型分布
      lxhmChartsData: [],
      lxhmLengendData: [],
      lxhmVisible: false,
      // 当前停电第一次短信发送失败分布
      dqtdChartsData: [],
      dqtdLengendData: [],
      dqtdVisible: false,
    }
  },
  mounted() {
    this.getCftzChartsData()
    this.getWtzChartsData()
    this.getJhtdChartsData()
    this.getLxhmChartsData()
    this.getDqtdChartsData()
  },
  methods: {
    //发生过重复通知抱怨分布
    getCftzChartsData() {
      this.wtzVisible = false
      this.fsgcftzbybGraph().then(res => {
        let data = []
        res.forEach(e => {
          if (e.fscftzby === '1') data.push({ name: "发生过重复通知抱怨", value: e.count })
          if (e.fscftzby === '0') data.push({ name: "未发生重复通知抱怨", value: e.count })
        });
        this.cftzChartsData = data
        this.cftzLengendData = ['发生过重复通知抱怨', '未发生重复通知抱怨']
        console.log(this.cftzChartsData, this.cftzLengendData);
        this.cftzVisible = true
      })
    },
    //发生过无通知抱怨分布
    getWtzChartsData() {
      this.wtzVisible = false
      this.fsgwtzbyGraph().then(res => {
        this.wtzChartsData = []
        let data = []
        res.forEach(e => {
          if (e.fswtzby === '1') data.push({ name: "发生过无通知抱怨", value: e.count })
          if (e.fswtzby === '0') data.push({ name: "未发生无通知抱怨", value: e.count })
        });
        this.wtzChartsData = data
        this.wtzLengendData = ['发生过无通知抱怨', '未发生无通知抱怨']
        this.wtzVisible = true
      })
    },
    // 计划停电前3天来电咨询停电相对比分布
    getJhtdChartsData() {
      this.jhtdVisible = false
      this.jhqstldGraph().then(res => {
        this.jhtdChartsData = []
        let data = []
        res.forEach(e => {
          data.push(e)
        });
        this.jhtdChartsData = data
        this.jhtdXNameData = ['0-0.3', '0.3-0.5', '0.5-1', '1-2', '2次及以上']
        this.jhtdVisible = true
      })
    },
    // 联系号码类型分布
    getLxhmChartsData() {
      this.lxhmVisible = false
      this.lxhmlxGraph().then(res => {
        this.lxhmChartsData = []
        let data = []
        let lengendData = []
        res.forEach(e => {
          data.push({ name: e.lxfx, value: e.count })
          lengendData.push(e.lxfx)
        });
        this.lxhmChartsData = data
        this.lxhmLengendData = lengendData
        this.lxhmVisible = true
      })
    },
    // 当前停电第一次短信发送失败分布
    getDqtdChartsData() {
      this.dqtdVisible = false
      this.dqtddycdxGraph().then(res => {
        this.dqtdChartsData = []
        let data = []
        res.forEach(e => {
          if (e.dqtddycdxsb === '0') data.push({ name: "当前第一次发送短信失败", value: e.count })
          if (e.dqtddycdxsb === '1') data.push({ name: "当前第一次发送短信成功", value: e.count })
        });
        this.dqtdChartsData = data
        this.dqtdLengendData = ['当前第一次发送短信失败', '当前第一次发送短信成功']
        this.dqtdVisible = true
      })
    },
  }
}
</script>

<style scoped lang="scss">
.distributionCharts {
  width: 100%;
  height: 800px;
  display: flex;
  flex-wrap: wrap;
  .chartsCon50,
  .chartsCon33 {
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    width: 50%;
    height: 50%;
    .charts-title {
      padding-left: 18px;
      border-left: 6px solid #1a84f4;
      font-weight: bold;
      margin: 20px 0;
    }
    .chartsItem {
      width: 100%;
      height: 100%;
    }
  }
  .chartsCon33 {
    width: 33.3%;
  }
}
</style>